/*个人中心*/
#personalwrapper{
    margin-top:20px;
   min-height:800px;
    overflow: hidden;
}
#personalwrapper .menu{
    width: 300px;
    min-height:800px;
    float: left;
}
#personalwrapper .content{
    width:800px;
    min-height:800px;
    float:right;
}
#personalwrapper .menutitle{
    margin-bottom:20px;
    text-align:center;
    font-size:18px;
    font-weight:bold;
}
#personalwrapper .lists a{
    width:100%;
    height:40px;
    line-height:40px;
    font-size:14px;
    border-bottom:1px solid #e4e4e4;
    padding:0 10px;
    display:block;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    -webkit-transition:all 0.4s ease;
    -o-transition:all 0.4s ease;
    transition:all 0.4s ease;
}
#personalwrapper .lists a.active{
    background-color:#ff9f00;
    color:#fff;
}
#personalwrapper .lists i{
    border-top:8px solid transparent;
    border-bottom:8px solid transparent;
    border-left:8px solid #fff;
    display:inline-block;
    float:right;
    margin-top:12px;
}



