/*用户订单*/
#orders a{
    width:100%;
    height:100px;
    line-height:100px;
    margin-bottom:20px;
    padding-bottom:20px;
    border-bottom:1px solid #e4e4e4;
    display:block;
}
#orders img{
    width:100px;
    height:100px;
    display:block;
    float:left;
}
#orders span{
    width:500px;
    height:100px;
    line-height:105px;
    margin-left:20px;
    font-size:16px;
    display:block;
    float:left;
}
#orders i{
    border-top:12px solid transparent;
    border-left:12px solid #e4e4e4;
    border-bottom:12px solid transparent;
    float:right;
    margin-top:40px;
    position:relative;
}
#orders i:after{
    content:" ";
    border-top:12px solid transparent;
    border-left:12px solid #fff;
    border-bottom:12px solid transparent;
    position: absolute;
    top:-12px;
    left:-13px;
}


