#login .header{
    width:100%;
    height:100px;
    line-height:100px;
    color:#000;
}
#login .header .logo{
    height: 100px;
    display:block;
}
#login .header img{
    width: 50px;
    height: 50px;
    margin-top: 25px;
    margin-right: 10px;
    float: left;
}
#login .header h1{
    font-size: 30px;
    float: left;
}
#login .content{
    width:100%;
    height:600px;
    background:url(./../images/login_bg.png) no-repeat center;
    background-size:cover;
}
#login .content .formwrapper{
    width:400px;
    height:400px;
    padding:0 40px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    margin-top:100px;
    background: #fff;
    float:right;
}
#login .content h1{
    margin-top:20px;
    margin-bottom:40px;
    text-align:center;
    line-height:50px;
    font-size:24px;
    color:#333;
    font-weight:400;
}
#login .content p{
    width:100%;
    height:40px;
    margin-bottom:20px;
    position:relative;
}
#login .content p label{
    width:40px;
    height:40px;
    display:inline-block;
}
#login .content p .countlabel{
    background:url(./../images/count.png) no-repeat center;
    background-size:30px 30px;
}
#login .content p .pwdlabel{
    background:url(./../images/pwd.png) no-repeat center;
    background-size:30px 30px;
}
#login .content p input{
    width:260px;
    height:40px;
    border:1px solid #e4e4e4;
    text-indent:5px;
    float:right;
}
#login .content p input[type=button]{
    width:100%;
    text-indent:0;
    background-color:#ff9f00;
    color:#fff;
    cursor:pointer;
    font-size:16px;
}
#login .content p input[type=button]:hover{
    -webkit-box-shadow:0 0 5px #ccc;
            box-shadow:0 0 5px #ccc;
}
#login .content p a{
    color:#069afd;
    text-decoration:underline;
}
#login .footer{
    text-align:center;
    line-height:40px;
    color:#999;
    font-size:14px;
}


