/*地图样式*/
.hotelmap .bmapmark{
    height:30px;
    line-height:30px;
    padding-left:40px;
    padding-right:8px;
    background-color: #fff;
    color: #333;
    border:1px solid #ccc;
    white-space:nowrap;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size:14px;
    border-radius:5px;
    position:absolute;
}
.hotelmap .bmapmark .bmapstar{
    height:30px;
    width:35px;
    text-align:center;
    color:#fff;
    background-color:#75cd15;
    border-radius:5px 0 0 5px;
    float:left;
    position:absolute;
    top:0;
    left:-1px;
}
.hotelmap  .bmaparrow{
    width:0;
    height:0;
    border-top:10px solid #fff;
    border-left:10px solid transparent;
    border-right:10px solid transparent;
    position:absolute;
    top:29px;
    left:38%;
}
.hotelmap .bmaparrow:before{
    content:"";
    width:0;
    height:0;
    border-top:10px solid #ccc;
    border-left:10px solid transparent;
    border-right:10px solid transparent;
    position:absolute;
    top:-9px;
    left:-10px;
    z-index:-1;
}


