/*页面底部区域样式*/
#footer{
    width:100%;
    height:415px;
    background-color:#111;
}
#footer .lists{overflow:hidden;}
#footer .item{
    width:255px;
    height:265px;
    margin-top:75px;
    margin-right:50px;
    float:left;
}
#footer .item:last-child{margin-right:0;}
#footer .listheader{
    height:40px;
    font-size:18px;
    font-weight:700;
    color:#fff;
    border-bottom:1px solid #2b2b2b;
}
#footer .webname{
    font-size:24px;
    font-weight:700;
    color:#ffffff;
}
#footer .listbody a{
    width:100%;
    height:35px;
    line-height:35px;
    color:#999;
    display:block;
    -webkit-transition:color .4s ease;
    -o-transition:color .4s ease;
    transition:color .4s ease;
}
#footer .listbody a:hover{
    color:#ffffff;
}
#footer .products p{
    width:100%;
    height:65px;
    margin-top:25px;
}
#footer .products a{
    width:65px;
    height:65px;
    margin-right:30px;
    -webkit-transition:all .2s ease;
    -o-transition:all .2s ease;
    transition:all .2s ease;
    float:left;
}
#footer .products a:hover{
    -webkit-transform:translate3d(0,-3px,0);
            transform:translate3d(0,-3px,0);
}
#footer .products img{
    width:100%;
    height:100%;
}
/*版权信息*/
#footer .copyright{
    font-size:14px;
    color:#999;
}


