/*个人中心*/
#individual .personalform{
   margin-top:20px;
}
#individual p{
    width:400px;
    height:40px;
    margin-bottom:10px;
}
#individual label{
    width:60px;
    margin-top:-2px;
    font-size:16px;
    white-space:nowrap;
    display:inline-block;
    vertical-align:middle;
    overflow:hidden;
}
#individual input[type=text]{
    width:300px;
    height:36px;
    border:1px solid #e4e4e4;
    text-indent:5px;
}
#individual input[type=button]{
    width:302px;
    height:36px;
    font-size:16px;
    margin-left:60px;
    background-color:#ff9f00;
    border-radius:5px;
    color:#fff;
    display:block;
    cursor:pointer;
}
#individual input[type=button]:hover{
    -webkit-box-shadow: 0 0 5px #ccc;
            box-shadow: 0 0 5px #ccc;
}


