/*注册页面*/
#regist .header{
    width:100%;
    height:100px;
    line-height:100px;
    color:#000;
}
#regist  .header .logo{
    height: 100px;
    display:block;
}
#regist  .header img{
    width: 50px;
    height: 50px;
    margin-top: 25px;
    margin-right: 10px;
    float: left;
}
#regist  .header h1{
    font-size: 30px;
    float: left;
}
#regist  .content{
    width:100%;
    height:700px;
    background:url(./../images/login_bg.png) no-repeat center;
    background-size:cover;
}
#regist  .backtologin{
    overflow:hidden;
    margin-bottom:10px;
}
#regist  .backtologin a{
    color:#069afd;
    text-decoration:underline;
    float:right;
}
#regist  .content .formwrapper{
    width:400px;
    height:600px;
    padding:0 40px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    margin-top:50px;
    background: #fff;
    float:right;
}
#regist  .content h1{
    margin-top:20px;
    margin-bottom:10px;
    text-align:center;
    line-height:50px;
    font-size:24px;
    color:#333;
    font-weight:400;
}
#regist  .content p{
    width:100%;
    height:40px;
    margin-bottom:20px;
    position:relative;
    overflow:hidden;
}
#regist  h2{
    font-size:18px;
    height:30px;
}
#regist  .content p input{
    width:100%;
    height:40px;
    border:1px solid #e4e4e4;
    text-indent:5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
#regist  .content p input[type=button]{
    width:100%;
    text-indent:0;
    background-color:#ff9f00;
    color:#fff;
    cursor:pointer;
    font-size:16px;
}
#regist  .content p input[type=radio],#regist  .content p input[type=checkbox]{
    width:13px;
    margin-right:3px;
    vertical-align: middle;
}
#regist  .content p input[type=button]:hover{
    -webkit-box-shadow:0 0 5px #ccc;
            box-shadow:0 0 5px #ccc;
}
#regist  #code{
    width:210px;
}
#regist  #codebtn{
    width:100px;
    margin-left:10px;
    border:0;
    float:right;
}
#regist  .content p a{
    color:#069afd;
    text-decoration:underline;
}
#regist  .footer{
    text-align:center;
    line-height:40px;
    color:#999;
    font-size:14px;
}



