/*顶部导航栏组件*/
#header{
    width:100%;
    height:85px;
    line-height:85px;
    background-color:#000;
    color:#fff;
}
#header .logo{
    width:300px;
    height:85px;
    float:left;
}
#header .logo img{
    width:45px;
    height:45px;
    margin-top:20px;
    margin-right:10px;
    float:left;
}
#header .logo h1{
    color:#fff;
    font-size:28px;
    float:left;
}
#header .navlist{
    width:600px;
    height:100%;
    overflow:hidden;
    white-space:nowrap;
    float:left;
}
#header .navlist a{
    height:83px;
    margin-right:80px;
    line-height:83px;
    font-size:16px;
    color:#fff;
    border-bottom:2px solid #000;
    display:inline-block;
    -webkit-transition:all 0.2s ease;
    -o-transition:all 0.2s ease;
    transition:all 0.2s ease;
}
#header .navlist a:hover{
    border-color:#ff9f00;
}
#header .login{
    width:300px;
    height:85px;
    text-align:right;
    float:right;
}
#header .login a{
    height:83px;
    margin-left:80px;
    line-height:83px;
    font-size:16px;
    color:#fff;
    border-bottom:2px solid #000;
    display:inline-block;
    -webkit-transition:all 0.2s ease;
    -o-transition:all 0.2s ease;
    transition:all 0.2s ease;
}
#header .login a:first-child{
    margin-left:0;
}
#header .login a:hover{
    border-color:#ff9f00;
}


