/*地图样式*/
#mymap .bmapmark{
    height:30px;
    line-height:28px;
    padding-left:35px;
    padding-right:5px;
    background-color: #fff;
    color: #333;
    border:1px solid #ccc;
    white-space:nowrap;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size:10px;
    border-radius:5px;
    position:absolute;
    cursor: pointer;
}
#mymap .bmapmark.active{
    height:40px;
    line-height:40px;
    padding-left:50px;
    padding-right:8px;
    font-size:13px;
    -webkit-box-shadow: 0 0 8px rgba(0,0,0,0.5);
            box-shadow: 0 0 8px rgba(0,0,0,0.5);
}
#mymap .bmapmark.active .bmapstar{
    height:40px;
    width:40px;
}
#mymap .bmapmark.active .bmaparrow{
    top:40px;
}
#mymap .bmapmark .bmapstar{
    height:30px;
    width:30px;
    text-align:center;
    color:#fff;
    background-color:#64a402;
    border-radius:5px 0 0 5px;
    float:left;
    position:absolute;
    top:0;
    left:-1px;
}
#mymap .bmaparrow{
    width:0;
    height:0;
    border-top:10px solid #fff;
    border-left:10px solid transparent;
    border-right:10px solid transparent;
    position:absolute;
    top:30px;
    left:38%;
}
#mymap .bmaparrow.emp{
    left:26%;
}
#mymap .bmaparrow:before{
    content:"";
    width:0;
    height:0;
    border-top:10px solid #ccc;
    border-left:10px solid transparent;
    border-right:10px solid transparent;
    position:absolute;
    top:-9px;
    left:-10px;
    z-index:-1;
}
/*视窗口开始*/
#mapInfowindow{
    width:220px;
    height:345px;
    background-color:#fff;
    padding:10px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    position:absolute;
    top:60px;
    right:30px;
    z-index:10;
    overflow:hidden;
    display:none;
    -webkit-box-shadow:0 0 8px rgba(0,0,0,0.3);
            box-shadow:0 0 8px rgba(0,0,0,0.3);
    border-radius:5px;
}
#mapInfowindow .pic{
    width:200px;
    height:150px;
    background-color:#e4e4e4;
    background-position:center;
    background-size:cover;
    border-radius:5px;
}
#mapInfowindow .info{
    width:200px;
    margin-top:5px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
#mapInfowindow .title{
    height:40px;
    line-height:40px;
    font-size:19px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
#mapInfowindow .stars{
    color:#fff;
    padding:1px 10px;
    border-radius:5px;
}
#mapInfowindow .counts{
    color:#999;
}
#mapInfowindow .category,#mapInfowindow .address,
#mapInfowindow .comments,#mapInfowindow .left p,
#mapInfowindow .right span,
#mapInfowindow .right a{
    height:22px;
    line-height:22px;
    font-size:14px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
#mapInfowindow .right span,
#mapInfowindow .right a{
    display:block;
}
#mapInfowindow .right s{
    font-size:16px;
    height:22px;
    line-height:22px;
    font-weight:700;
    color:red;
    text-decoration:line-through;
}
#mapInfowindow .right span{
    color:#75cd15;
}
#mapInfowindow .right a{
    width:200px;
    height:30px;
    line-height:30px;
    margin-top:10px;
    text-align:center;
    border-radius:5px;
    background-color: #75cd15;
    color:#fff;
}
#mapInfowindow .category{
    color:#007aff;
}
#mapInfowindow .category span{
    margin-right:5px;
}
#mapInfowindow .details{
    width:100%;
    height:105px;
}
/*视窗口结束*/
#mapHotelcount{
    padding:5px 15px;
    border-radius:20px;
    background-color:#64a402;
    color:#fff;
    font-size:12px;
    position:absolute;
    top:20px;
    right:30px;
    z-index: 10;
    -webkit-box-shadow: 0 0 8px rgba(0,0,0,0.3);
            box-shadow: 0 0 8px rgba(0,0,0,0.3);
}
/*筛选*/
#mapFilter{
    width:300px;
    padding:10px;
    background-color:#fff;
    border-radius:5px;
    position:absolute;
    left:30px;
    top:20px;
    -webkit-box-shadow:0 0 5px rgba(0,0,0,0.4);
            box-shadow:0 0 5px rgba(0,0,0,0.4);
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
#mapFilter p{
    width:100%;
    height:40px;
    line-height:40px;
    margin-bottom:10px;
}
#citysearch{
    width:200px;
    height:38px;
    font-size:16px;
    text-indent:5px;
    border:1px solid #e4e4e4;
    border-right-color: #fff;
    float:left;
}
#searchbtn{
    width:78px;
    height:40px;
    color:#fff;
    background-color:#64a402;
    font-size:16px;
    float:right;
    cursor:pointer;
}
#mapFilter .distances i{
    width:22px;
    height:22px;
    text-align:center;
    line-height:22px;
    margin-right:10px;
    font-size:12px;
    color:#999;
    border:1px solid #999;
    border-radius:4px;
    display:inline-block;
    cursor:pointer;
}
#mapFilter .distances .active{
    color:#fff;
    background-color: #64a402;
    border-color:#64a402;
}
#mapFilter .distances i:first-child{
    margin-left:10px;
}
#mapFilter .distances i:last-child{
    margin-right:0;
}
/*跳转到列表页*/
#tohotellist {
    width:100%;
    height:35px;
    line-height:35px;
    text-align:center;
    color:#fff;
    background-color: #ff9f00;
    border-radius:5px;
    display:block;
}


