/*公司*/
#company{
    margin:20px auto;
    overflow:hidden;
}
#company .lists{
    width:300px;
    min-height:800px;
    float:left;
}
#company .lists h1{
    font-size:24px;
    height:40px;
}
#company .lists a{
    width:100%;
    height:50px;
    padding:0 10px;
    line-height:50px;
    border-bottom:1px solid #e4e4e4;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    font-size:16px;
    display:block;
    -webkit-transition:all .4s ease;
    -o-transition:all .4s ease;
    transition:all .4s ease;
}
#company .lists i{
    margin-top:17px;
    border-top:8px solid transparent;
    border-left:8px solid #fff;
    border-bottom:8px solid transparent;
    display:inline-block;
    float:right;
}
#company .lists .active{
    color:#fff;
    background-color: #ff9f00;
    border-color:#ff9f00;
}
#company .content{
    width:800px;
    min-height:800px;
    padding-left:20px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    float:right;
}
#company .content p{
    text-indent:20px;
    line-height:30px;
}
#company .content img{
    width:780px;
    margin-bottom:15px;
}
#company .content h2{
    line-height:40px;
    margin-top:20px;
}
/*合作伙伴开始*/
#company #cooprator a{
    width:200px;
    height:70px;
    margin-right:50px;
    margin-bottom:30px;
    float:left;
    display:block;
}
#company #cooprator a:nth-child(3n){
    margin-right:0;
}
#company #cooprator img{
    width:100%;
    height:100%;
    background-color: #cccccc;
}
#company #cooprator a:hover img{
    -webkit-animation: rotate 1s ease;
            animation: rotate 1s ease;
}
@-webkit-keyframes rotate{
    0%{
        -webkit-transform:rotateY(0deg);
                transform:rotateY(0deg);
    }
    100%{
        -webkit-transform:rotateY(360deg);
                transform:rotateY(360deg);
    }
}
@keyframes rotate{
    0%{
        -webkit-transform:rotateY(0deg);
                transform:rotateY(0deg);
    }
    100%{
        -webkit-transform:rotateY(360deg);
                transform:rotateY(360deg);
    }
}
/*合作伙伴结束*/
/*下载开始*/
#download .download_btn{
    width:120px;
    height:50px;
    line-height:50px;
    text-align:center;
    display:block;
    float:left;
    margin-top:20px;
    border-radius:5px;
    background-color:#ff9f00;
    color:#fff;
}
#download .download_btn:hover{
    -webkit-box-shadow:0 0 5px #999;
            box-shadow:0 0 5px #999;
}
/*下载结束*/


