/*酒店列表*/
/*搜索框开始*/
#hotel .searchwrapper{
    width:1200px;
    height:65px;
    line-height:65px;
    margin:30px auto;
    background-color:#fff;
    font-size:20px;
    border:1px solid #e4e4e4;
    position:relative;
}
#hotel .searchwrapper .input{
    width:1030px;
    padding:0 30px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    float:left;
}
#hotel .searchwrapper input{
    font-size:20px;
}
/*搜索框结束*/
#hotel .wrapper{
    padding:20px 0;
    background-color:#eee;
}
/*左边筛选区域开始*/
/*查看地图开始*/
#hotel .filter{
    width:190px;
    float:left;
}
#hotel .filter .checkmap{
    width:100%;
    height:100px;
    border:5px solid #fff;
    position:relative;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow:hidden;
}
#hotel .filter .checkmap img{
    width:100%;
    height:100%;
    -webkit-transition:all 0.4s ease;
    -o-transition:all 0.4s ease;
    transition:all 0.4s ease;
}
#hotel .filter .checkmap a{
    width:80px;
    height:30px;
    text-align:center;
    line-height:30px;
    background-color:#ffffff;
    position:absolute;
    left:48px;
    top:30px;
    z-index:1;
    cursor:pointer;
}
#hotel .filter .checkmap:hover img{
    -webkit-transform: scale(2);
        -ms-transform: scale(2);
            transform: scale(2);
}
/*查看地图结束*/
/*筛选条件开始*/
#hotelfilter{
    width:100%;
    margin-top:10px;
    padding:5px;
    background:#fff;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
#hotelfilter .title{
    width:100%;
    text-align:center;
    height:50px;
    line-height:50px;
    font-size:20px;
    font-weight:700;
}
#hotelfilter .fiterwrapper{
    width:100%;
}
#hotelfilter .item{
    width:100%;
    padding-bottom:20px;
}
#hotelfilter .item .itemtitle{
    line-height:35px;
    font-size:16px;
    font-weight:700;
}
#hotelfilter .item .itemwrapper span{
    width:87px;
    margin-top:6px;
    margin-right:6px;
    line-height:30px;
    text-align:center;
    border-radius:5px;
    background:#f4f4f4;
    color:#333;
    font-size:14px;
    display:inline-block;
    cursor:pointer;
}
#hotelfilter .item .itemwrapper span:nth-child(even){
    margin-right:0;
}
#hotelfilter .item .itemwrapper .active{
    color:#fff;
    background:#75cd15;
}
/*酒店设施开始*/
#hotelfilter #hoteldevices span{
    width:56px;
    margin-right:6px;
    background-color:#ffffff;
    color:#000000;
    font-size:12px;
    opacity: 0.6;
}
#hotelfilter #hoteldevices .active{
    opacity:1;
}
#hotelfilter #hoteldevices span:nth-child(3n){
    margin-right:0;
}
#hotelfilter #hoteldevices img{
    width:40px;
    height:40px;
    margin:0 auto;
    display:block;
}
#hotelfilter #hoteldevices i{
    width:100%;
    height:15px;
    line-height:15px;
    overflow:hidden;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    white-space:nowrap;
    display:block;
}
/*酒店设施结束*/
/*地标开始*/
#hotelfilter .hotelmarks{
    width:100%;
    height:35px;
    line-height:35px;
    margin-top:20px;
    padding:0 5px;
    font-size:14px;
    border:1px solid #e4e4e4;
    position:relative;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    white-space:nowrap;
    cursor:pointer;
}
#hotelfilter .hotelmarks i{
    width:9px;
    height:6px;
    background:url(./../images/done_icon.png) no-repeat center;
    display:inline-block;
    float:right;
    margin-top:14px;
}
#hotelfilter .listwrapper{
    width:180px;
    margin-top:2px;
    background-color:#ffffff;
    -webkit-box-shadow:0 1px 5px rgba(0,0,0,0.2);
            box-shadow:0 1px 5px rgba(0,0,0,0.2);
    display:none;
}
#hotelfilter .listwrapper p{
    line-height:35px;
    padding:0 5px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    border-bottom:1px solid #fff;
    white-space:nowrap;
    cursor:pointer;
    overflow:hidden;
}
#hotelfilter .listwrapper .active{
    color: #fff;
    background: #75cd15;
}
#hotelfilter .listwrapper p:hover{
     background-color: #f7f7f7;
 }
/*地标结束*/
/*筛选条件结束*/
/*筛选重置按钮*/
#resetfilter{
    width:100%;
    height:35px;
    margin-bottom: 20px;
    text-align:center;
    line-height:35px;
    background:#ff9f00;
    color:#fff;
    font-size:14px;
    border-radius:5px;
    cursor:pointer;
    -webkit-transition:all 0.4s ease;
    -o-transition:all 0.4s ease;
    transition:all 0.4s ease;
}
#resetfilter:hover{
    -webkit-box-shadow:0 1px 5px rgba(0,0,0,0.2);
            box-shadow:0 1px 5px rgba(0,0,0,0.2);
}
/*筛选重置按钮*/
/*左边筛选区域结束*/
/*中间列表区域开始*/
#hotel .hotellist{
    width:800px;
    padding-bottom: 20px;
    margin-left:10px;
    float:left;
}
/*顶部筛选开始*/
#hotel .headerfilter{
    width:100%;
    height:30px;
    line-height:30px;
}
#hotel .headerfilter .info{
    width:300px;
    height:100%;
    float:left;
}
#hotel .headerfilter .sort{
    width:300px;
    height:100%;
    text-align:right;
    float:right;
    position:relative;
}
#hotel .headerfilter .sort .title{
    font-size:18px;
    font-weight:700;
    display:inline-block;
}
#hotel .headerfilter .sort .option{
    width:180px;
    height:30px;
    margin-left:10px;
    padding:0 5px;
    text-align:left;
    background-color:#ffffff;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    display:inline-block;
    cursor:pointer;
}
#hotel .headerfilter .sort i{
    width:9px;
    height:6px;
    background:url(./../images/done_icon.png) no-repeat center;
    display:inline-block;
    float:right;
    margin-top:14px;
}
#hotel .headerfilter .sort .listwrapper{
    width:180px;
    text-align:left;
    background-color:#fff;
    position:absolute;
    z-index: 2;
    right:0;
    top:32px;
    -webkit-box-shadow: 0 1px 5px rgba(0,0,0,0.2);
            box-shadow: 0 1px 5px rgba(0,0,0,0.2);
    display:none;
}
#hotel .headerfilter .sort .listwrapper p{
    color:#666;
    padding:0 5px;
    border-bottom:1px solid #fff;
    cursor:pointer;
}
#hotel .headerfilter .sort .listwrapper .active{
    color: #fff;
    background: #75cd15;
}
#hotel .headerfilter .listwrapper p:hover{
    background:#f7f7f7;
}
/*顶部筛选结束*/
/*酒店列表开始*/
#hotel .hotellist .lists{
    min-height:1000px;
}
#hotel .hotellist .listitem{
    width:100%;
    min-height:200px;
    background:#fff;
    margin-top:10px;
}
#hotel .hotellist .hotelpic{
    width:230px;
    height:200px;
    background-color:#ccc;
    background-position:center;
    background-size:cover;
    background-repeat:no-repeat;
    float:left;
    cursor:pointer;
}
/*中间酒店信息开始*/
#hotel .hotellist .information{
    width:360px;
    height:200px;
    margin:0 10px;
    float:left;
}
#hotel .hotellist .information .hotelname{
    width:100%;
    height:40px;
    line-height:40px;
    font-size:20px;
    overflow:hidden;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    white-space:nowrap;
}
#hotel .hotellist .information .category{
    width:100%;
    height:30px;
    line-height:30px;
    font-size:16px;
    color:#069afd;
    overflow:hidden;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    white-space:nowrap;
}
#hotel .hotellist .information .category span{
    margin-right:10px;
}
#hotel .hotellist .information .site{
    width:100%;
    height:30px;
    line-height:30px;
    font-size:16px;
    overflow:hidden;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    white-space:nowrap;
}
#hotel .hotellist .information .comment{
    width:100%;
    height:30px;
    line-height:30px;
    font-size:16px;
    color:#666;
    overflow:hidden;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    white-space:nowrap;
}
#hotel .hotellist .information .comment span{
    height:100%;
    padding:0 10px;
    margin-right:10px;
    color:#fff;
    background-color:#75cd15;
    border-radius:5px;
    display:inline-block;
}
#hotel .hotellist .information .shops{
    width:100%;
    height:70px;
    margin-top:10px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    border-top:1px solid #ddd;
    /*background:url("./../images/black_right_arrow.png") no-repeat right 20px;*/
    background-size:20px 20px;
    position:relative;
    overflow:hidden;
}
#hotel .hotellist .information .shops .shopwrapper{
    height:70px;

}
#hotel .hotellist .information .shops p{
    height:100%;
    line-height:30px;
    text-align:center;
    padding:0 20px;
    display:inline-block;
}
#hotel .hotellist .information .shops p:first-child{
    padding-left:0;
}
#hotel .hotellist .information .shops span{
    display:block;
}
/*中间酒店信息结束*/
#hotel .hotellist .price{
    width:190px;
    height:180px;
    margin-top:10px;
    text-align:center;
    border-left:1px solid #ddd;
    float:right;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
#hotel .hotellist .price p{
    height:35px;
    line-height:35px;
}
#hotel .hotellist .price .shopname{
    font-size:16px;
    margin-top:15px;
}
#hotel .hotellist .price .maxprice{
    font-size:28px;
    font-weight:bold;
    color:#dd0e27;
    text-decoration:line-through;
}
#hotel .hotellist .price .minprice{
    font-size:24px;
    color:#64a402;
}
#hotel .hotellist .price .btn{
    width:140px;
    margin:0 auto;
    padding:3px 0;
    color:#fff;
    font-size:16px;
    background:#64a402;
    display:block;
    cursor:pointer;
}
#hotel .hotellist .price .btn:hover{
    -webkit-box-shadow:0 0 5px rgba(0,0,0,0.4);
            box-shadow:0 0 5px rgba(0,0,0,0.4);
}
/*酒店详细内容开始*/
#hotel .hoteldetail{
    width:100%;
    padding:20px;
    background-color:#fff;
    -webkit-box-shadow: 0 2px 5px rgba(0,0,0,0.4);
            box-shadow: 0 2px 5px rgba(0,0,0,0.4);
    display:none;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
#hotel .hoteldetail .detailheader{
    width:100%;
    height:50px;
    margin-bottom:20px;
    border-bottom:1px solid #ddd;
    overflow:hidden;
}
#hotel .hoteldetail .tablist{
    width:300px;
    height:50px;
    line-height:50px;
    font-size:16px;
    float:left;
}
#hotel .hoteldetail .tablist p{
    height:48px;
    padding:0 10px;
    border-bottom:2px solid #fff;
    display:inline-block;
    cursor:pointer;
    -webkit-transition:all 0.2s ease;
    -o-transition:all 0.2s ease;
    transition:all 0.2s ease;
}
#hotel .hoteldetail .tablist p:hover{
    color:#ff9f00;
    border-color:#ff9f00;
}
#hotel .hoteldetail .tablist .active{
    color:#ff9f00;
    border-color:#ff9f00;
}
/*电话*/
#hotel .hoteldetail .telephone{
    width:180px;
    height:50px;
    line-height:50px;
    font-size:14px;
    text-align:right;
    background:url(./../images/phone_icon.png) no-repeat left center;
    background-size:16px 16px;
    float:right;
}
/*详情开始*/
#hotel .hoteldetail .detailitem{
   display:none;
    overflow:hidden;
}
#hotel .hoteldetail .detailpics p{
    width:375px;
    height:210px;
    margin-left:10px;
    margin-bottom:10px;
    background-color: #ccc;
    background-repeat:no-repeat;
    background-size: cover;
    display:inline-block;
}
#hotel .hoteldetail .detailpics p:first-child{
    margin-left:0;
}
#hotel .hoteldetail .text span{
    color:#069afd;
    cursor:pointer;
}
/*酒店设施*/
#hotel .hoteldetail .devices p{
    width:84px;
    margin-top:10px;
    text-align:center;
    float:left;
}
#hotel .hoteldetail .devices img{
    width:40px;
    height:40px;
    margin:0 auto;
    display:block;
}
#hotel .hoteldetail .devices span{
    margin:0 5px;
    height:20px;
    display:block;
    overflow:hidden;
}
#hotel .hoteldetail .devicestitle{
    font-size:16px;
    font-weight:700;
    margin-top:10px;
}
/*详情结束*/
/*用户评价开始*/
#hotel .hoteldetail .usericon{
    width:50px;
    height:50px;
    border-radius:50px;
    background-color:#ccc;
    background-size:cover;
    background-repeat:no-repeat;
    background-position: center;
    float:left;
}
#hotel .hoteldetail .commentitem{
    margin-bottom:20px;
    padding-bottom:20px;
    border-bottom:1px solid #e4e4e4;
    overflow:hidden;
}
#hotel .hoteldetail .commentitem:last-child{
    border-bottom:0;
}
#hotel .hoteldetail .content{
    width:690px;
    float:right;
}
#hotel .hoteldetail .content .top{
    margin-bottom:10px;
    overflow:hidden;
}
#hotel .hoteldetail .content .score{
    color:#75cd15;
    float:left;
}
#hotel .hoteldetail .content .time{
    color:#999;
    float:right;
}
/*用户评价结束*/
/*酒店详细内容结束*/
/*酒店列表结束*/
/*点击加载更多开始*/
#pagemore{
    margin-top:20px;
    height:40px;
}
#pagemore p{
    width:300px;
    height:100%;
    text-align:center;
    line-height:40px;
    margin:0 auto;
    background-color:#e4e4e4;
    font-size:14px;
    color:#666;
    cursor:pointer;
    display:block;
    -webkit-user-select:none;
       -moz-user-select:none;
        -ms-user-select:none;
            user-select:none;
}
#pagemore p:hover{
    -webkit-box-shadow: 0 0 5px #bbb;
            box-shadow: 0 0 5px #bbb;
}
/*点击加载更多结束*/
/*中间列表区域结束*/
/*右边比较区域开始*/
#hotel .compare{
    width:190px;
    float:right;
}
/*右侧广告开始*/
#hotel .compare .adv{
    width:100%;
    height:180px;
    background:url(./../images/Advertisement_icon.png) no-repeat center;
    background-size:100% 100%;
    position:relative;
}
#hotel .compare .adv .btnwrapper{
    width:180px;
    height:24px;
    margin:5px;
    line-height:24px;
    text-align:right;
    color:#666;
    font-size:12px;
    position:absolute;
    bottom:0;
}
#hotel .compare .adv i{
    width:50px;
    height:20px;
    line-height:20px;
    text-align:center;
    margin-right:5px;
    background-color:#fff;
    -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.4);
            box-shadow: 0 0 5px rgba(0,0,0,0.4);
    vertical-align: middle;
    display:inline-block;
}
#hotel .compare .adv s{
    width:20px;
    height:20px;
    background:url(./../images/add_close_icon.png) no-repeat center #fff;
    background-size:100%;
    -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.4);
            box-shadow: 0 0 5px rgba(0,0,0,0.4);
    vertical-align: middle;
    display:inline-block;
    cursor:pointer;
}
/*右侧广告结束*/
#hotel .pricecompare{
    width:100%;
    margin-top:10px;
    padding:5px;
    padding-bottom: 20px;
    background:#fff;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
#hotel .pricecompare .title{
    width:100%;
    line-height:40px;
    font-size:16px;
    font-weight:700;
}
#hotel .pricecompare .shopswrapper{
    overflow:hidden;/*消除浮动*/
}
#hotel .pricecompare .shopswrapper a{
    width:56px;
    height:56px;
    margin-right:6px;
    margin-bottom:6px;
    display:block;
    float:left;
}
#hotel .pricecompare .shopswrapper a:nth-child(3n){
    margin-right:0;
}
#hotel .pricecompare .shopswrapper img{
    width:100%;
    height:100%;
}
#hotel .pricecompare .btn{
    width:100%;
    height:35px;
    line-height:35px;
    text-align:center;
    font-size:14px;
    color:#fff;
    background-color:#ff9f00;
    border-radius:5px;
    cursor:pointer;
}
#hotel .pricecompare .btn:hover{
    -webkit-box-shadow:0 1px 5px rgba(0,0,0,0.2);
            box-shadow:0 1px 5px rgba(0,0,0,0.2);
}
/*右边比较区域结束*/

/*当屏幕小于1500px时*/
@media screen and (max-width:1500px){
    /*搜索框开始*/
    #hotel .searchwrapper{
        width:1200px;
        height:60px;
        line-height:60px;
        margin:30px auto;
        font-size:16px;
    }
    #hotel .searchwrapper input{
        font-size:16px;
    }
    /*搜索框结束*/
    /*中间酒店信息开始*/
    #hotel .hotellist .information .hotelname{
        font-size:19px;
    }
    #hotel .hotellist .price .maxprice {
        font-size: 25px;
    }
    #hotel .hotellist .price .minprice{
        font-size:20px;
    }
    /*中间酒店信息结束*/
}


