/*首页样式*/
/*顶部导航栏开始*/
#homeheader{
    width:100%;
    position:absolute;
    top:0;
    left:0;
}
#homeheader .wrapper{
    max-width:1920px;
    height:70px;
    line-height:70px;
    margin:0 auto;
    padding:0 45px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
#homeheader a{
    margin-left:50px;
    font-size:16px;
    color:#6795b8;
    -webkit-transition: color 0.4s ease;
    -o-transition: color 0.4s ease;
    transition: color 0.4s ease;
}
#homeheader a:hover{
    color:#fff;
}
#homeheader .left{
    float:left;
}
#homeheader .left a:first-child{
    margin:0;
}
#homeheader .right{
    float:right;
}
/*顶部导航栏结束*/
/*focus开始*/
#homefocus{
    width:100%;
    max-height:1080px;/*高度最大支持1080px*/
    background:url(./../images/focus.png) no-repeat center #cccccc;
    background-size:100% 100%;
}
#homefocus .logo{
    padding-top:6%;
    text-align:center;
    color:#fff;
}
#homefocus .logo img{
    width:70px;
    height:70px;
}
#homefocus .logotitle{
    font-size:40px;
    line-height:60px;
}
#homefocus .logosubtitle{
    font-size:24px;
}
#homefocus .slogan{
    margin-top:7%;
    margin-bottom:12%;
    font-size:48px;
}
/*搜索框开始*/
#searchlist{
    width:640px;
    margin:0 auto;
    overflow:hidden;
}
#searchlist .item{
    width:25%;
    text-align:center;
    color:#fff;
    float:left;
    cursor:pointer;
}
#searchlist .item p{
    line-height:70px;
    font-size:24px;
}
#searchwrapper{
    width:1200px;
    height:140px;
    line-height:65px;
    font-size:20px;
    overflow:hidden;
    position:relative;
}
#searchwrapper .searchbtn{
    height:65px;
}
#searchwrapper input{
    font-size:20px;
}
#searchwrapper .boxwrapper{
    width:4800px;
    height:140px;
    position:absolute;
    left:0;
    top:0;
    -webkit-transition:left 0.4s ease;
    -o-transition:left 0.4s ease;
    transition:left 0.4s ease;
}
#searchwrapper .item{
    width:1200px;
    height:140px;
    float:left;
}
#searchwrapper .item .input{
    width:1030px;
    padding:0 30px;
    background-color:#fff;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    float:left;
}
/*搜索框结束*/
/*商家选择开始*/
.shopcheck{
    height:30px;
    margin-top:20px;
    color:#fff;
    font-size:23px;
    white-space: nowrap;
    overflow:hidden;
    float:left;
}
.shopcheck span{
    line-height:30px;
    padding-left:30PX;
    margin-right:50px;
    background:url(./../../src/images/unchecked.png) no-repeat left center;
    cursor:pointer;
    float:left;
}
.shopcheck .active{
    color:#ff9f00;
    background:url(./../../src/images/checked.png) no-repeat left center;
}
.shopcheck span:last-child{
    margin:0;
}
/*商家选择结束*/
/*自动补全*/
/*.autowrapper{*/
    /*width:100%;*/
    /*position:absolute;*/
    /*z-index:3;*/
    /*display:block;*/
    /*margin-top:-60px;*/
/*}*/
/*focus结束*/
/*返回顶部开始*/
#totop{
    width:100%;
    margin-top:-80px;
}
#totop p{
    width:40px;
    height:40px;
    margin:0 auto;
    background:url(./../images/top_icon.png) no-repeat center;
    background-size:50px 27px;
    opacity:0.5;
    -webkit-transition:all 0.4s ease;
    -o-transition:all 0.4s ease;
    transition:all 0.4s ease;
    cursor:pointer;
}
#totop p:hover{
    opacity:1;
}
/*返回顶部结束*/
/*侧边下载*/
#sidedownload{
    width:150px;
    height:100px;
    padding:10px;
    text-align:center;
    background:url(./../images/right_icon.png) no-repeat 110px center;
    background-size:20px 30px;
    background-color:rgba(0,0,0,0.5);
    color:#fff;
    font-size:14px;
    border-radius: 0  50px 50px 0;
    position:fixed;
    left:0;
    bottom:35px;
    overflow:hidden;
    opacity:1;
    z-index: 4;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor:pointer;
    display:block;
    -webkit-transition:all 1s ease;
    -o-transition:all 1s ease;
    transition:all 1s ease;
}
#sidedownload img{
    width:40px;
    height:40px;
    margin-left:-50px;
}
#sidedownload p{
    height:20px;
    line-height:20px;
    margin-left:-50px;
}
#sidedownpop{
    width:100%;
    height:200px;
    background-color:rgba(0,0,0,0.5);
    position:fixed;
    left:-100%;
    bottom:0;
    color:#fff;
    overflow:hidden;
    -webkit-transition:all 1s ease;
    -o-transition:all 1s ease;
    transition:all 1s ease;
}
#sidedownpop .logo{
    width:100px;
    height:100px;
    margin-top:50px;
    display:block;
    float:left;
}
#sidedownpop .left,#sidedownpop .right{
    float:left;
}
#sidedownpop .left{
    width:680px;
    margin-left:40px;
}
#sidedownpop .left h3{
    margin-top:40px;
    margin-bottom:10px;
    font-size:40px;
}
#sidedownpop .left h1{
    font-size:48px;
}
#sidedownpop .right{
    width:290px;
    margin-right:85px;
    float:right;
}
#sidedownpop .right .code{
    width:120px;
    margin-top:27px;
    margin-right:49px;
    display:inline-block;
    text-align:center;
}
#sidedownpop .right .code:last-child{
    margin-right:0;
}
#sidedownpop .right .code img{
    width:120px;
    height:120px;
}
#closebtn{
    width:12px;
    height:12px;
    background:url(./../images/close_icon.png) no-repeat center;
    background-size:cover;
    position:absolute;
    right:0;
    top:10px;
    opacity:.5;
    cursor:pointer;
}
#closebtn:hover{
    opacity:1;
}
/*日历开始*/
.datewrapper{
    width:100%;
    height:100%;
    background-color:rgba(0,0,0,0.4);
    position:fixed;
    top:0;
    left:0;
    z-index:10;
    display:none;
}
/*日历结束*/

/*当屏幕小于1500时*/
@media screen and (max-width:1500px){
    /*focus开始*/
    #homefocus .logo{
        padding-top:4%;
    }
    #homefocus .logo img{
        width:55px;
        height:55px;
    }
    #homefocus .logotitle{
        font-size:35px;
    }
    #homefocus .logosubtitle{
        font-size:18px;
    }
    #homefocus .slogan{
        margin-top:4%;
        margin-bottom:9%;
        font-size:42px;
    }
    /*搜索框开始*/
    #searchlist .item p{
        line-height:40px;
        font-size:18px;
    }
    #searchwrapper{
        height:120px;
        line-height:60px;
        font-size:16px;
    }
    #searchwrapper .searchbtn{
        height:60px;
    }
    #searchwrapper input{
        font-size:16px;
    }
    #searchwrapper .boxwrapper{
        height:120px;
    }
    #searchwrapper .item{
        height:120px;
    }
    /*搜索框结束*/
    /*商家选择开始*/
    .shopcheck{
        margin-top:10px;
        font-size:20px;
    }
    /*商家选择结束*/
    /*侧边下载*/
    #sidedownload{
        width:125px;
        height:80px;
        padding:8px;
        background:url(./../images/right_icon.png) no-repeat 90px center;
        background-size:15px 25px;
        background-color:rgba(0,0,0,0.5);
    }
    #sidedownload img{
        width:35px;
        height:35px;
        margin-left:-40px;
    }
    #sidedownload p{
        height:13px;
        line-height:13px;
        font-size:12px;
        margin-left:-40px;
    }
    #sidedownpop{
        height:150px;
    }
    #sidedownpop .logo{
        width:70px;
        height:70px;
        margin-top:40px;
    }
    #sidedownpop .left h3{
        margin-top:30px;
        margin-bottom:2px;
        font-size:30px;
    }
    #sidedownpop .left h1{
        font-size:35px;
    }
    #sidedownpop .right .code{
        width:90px;
        margin-top:20px;
        margin-right:60px;
    }
    #sidedownpop .right .code:last-child{
        margin-right:0;
    }
    #sidedownpop .right .code img{
        width:90px;
        height:90px;
    }
}


