/*设置日历的大小*/
.datepickcalendar{
    width:300px;
    height:332px;
    position:absolute;
    top:50%;
    left:50%;
    margin-left:-150px;
    margin-top:-166px;
    background:#fff;
    overflow:hidden;
    cursor:pointer;
    display:none;
}
/*设置日历顶部盒子*/
.datepickcalendar .calendar-title-box{
    position:relative;
    width:100%;
    height:50px;
    line-height:45px;
    text-align:center;
    border-bottom:1px solid #eee;
    background-color: #ff9f00;
}
.datepickcalendar .calendar-title-box span{
    color:#fff;
}
.datepickcalendar .calendar-title{
    font-size:16px;
    font-weight:700;
    color:#666;
}
.datepickcalendar .calendar-body-box .weeks{
    height:22px;
    line-height:22px;
}
.datepickcalendar .calendar-body-box th{
    font-size:12px;
    font-weight:400;
    color:#666;
}
.datepickcalendar td{
    font-size:12px;
    border:1px solid #eee;
}
/*设置上个月按钮图标*/
.datepickcalendar .prev-month{
    position:absolute;
    top:19px;
    left:15px;
    display:inline-block;
    width:0;
    height:0;
    border-left:0;
    border-top:6px solid transparent;
    border-right:8px solid #fff;
    border-bottom:6px solid transparent;
    cursor:pointer;
}
/*设置下个月的按钮图标*/
.datepickcalendar .next-month{
    position:absolute;
    top:19px;
    right:15px;
    display:inline-block;
    width:0;
    height:0;
    border-right:0;
    border-top:6px solid transparent;
    border-left:8px solid #fff;
    border-bottom:6px solid transparent;
    cursor:pointer;
}
/*设置日历表格样式*/
.datepickcalendar .calendar-table{
    width:100%;
    border-collapse:collapse;
    text-align:center;
}
/*表格行高*/
.datepickcalendar .calendar-table tr{
    height:42px;
    line-height:42px;
}
.datepickcalendar .calendar-table tr .choose{
    background-color:#ff9f00;
    color:#fff;
}
/*当天颜色特殊显示*/
.datepickcalendar .currentDay{
    font-size:16px;
    font-weight:700;
    color:#75cd15;
}
/*本月 文字颜色*/
.datepickcalendar .currentMonth{
    color:#333;
}
/*其他月颜色*/
.datepickcalendar .otherMonth{
    color:#999;
}


