/*合作伙伴*/
#parterner .wrapper{
    min-height:100px;
    overflow:hidden;
}
#parterner a{
    width:200px;
    height:70px;
    margin-right:50px;
    margin-bottom:30px;
    float:left;
    display:block;
}
#parterner a:nth-child(5n){
    margin-right:0;
}
#parterner img{
    width:100%;
    height:100%;
    background-color: #cccccc;
}
#parterner a:hover img{
    -webkit-animation: rotate 1s ease;
            animation: rotate 1s ease;
}
@-webkit-keyframes rotate{
    0%{
        -webkit-transform:rotateY(0deg);
                transform:rotateY(0deg);
    }
    100%{
        -webkit-transform:rotateY(360deg);
                transform:rotateY(360deg);
    }
}
@keyframes rotate{
    0%{
        -webkit-transform:rotateY(0deg);
                transform:rotateY(0deg);
    }
    100%{
        -webkit-transform:rotateY(360deg);
                transform:rotateY(360deg);
    }
}


